___CSS_LOADER_EXPORT___.push([module.id, `/* カーソルを非表示にする */
* {
    cursor: none !important;
}

#cursor, #stalker {
    position: fixed;
    pointer-events: none;
    transform: translate(-50%, -50%);
    mix-blend-mode: difference;
    pointer-events: none; /* クリックを無効化 */
    z-index: 9999; /* 最前面に配置 */
    transition: transform 0.1s ease-out;
}

/* カーソルのスタイル #894646*/
#cursor {
    width: 1px;
    height: 26px;
    transform: translate(300%, -15%) rotate(-40deg);
    border: 1px solid #fff;
    box-shadow: 0 0 3px rgba(25, 25, 25);
    transition: width 0.3s ease-in-out, height 0.3s ease-in-out;
    z-index: 10000;
}

#stalker {
    width: 30px;
    height: 30px;
    border: 1px solid #fff;
    border-radius: 50%;
    transition: opacity 0.3s ease-in-out, width 0.3s ease-in-out, height 0.3s ease-in-out;
    box-shadow: 0 0 4px rgba(25, 25, 25);
    z-index: 9999;
}

/* ホバー時のスタイル */
.cursor--hover {
    width: 1px !important;
    height: 40px !important;
    border: 1px solid var(--border);
    opacity: 0.5;
}

.stalker--hover {
    width: 60px !important;
    height: 60px !important;
    border: 1px solid #894646;
    background: var(--hover);
    box-shadow: 0 0 10px rgba(217, 217, 217, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10000;
}
.stalker--hover::after {
    content: "Click";
    font-family: "Jost", sans-serif;
    font-size: 2rem;
    font-weight: 500;
    color: var(--bg);
}`, "",{"version":3,"sources":["webpack://./src/style/cursor.css"],"names":[],"mappings":"AAAA,gBAAgB;AAChB;IACI,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,oBAAoB;IACpB,gCAAgC;IAChC,0BAA0B;IAC1B,oBAAoB,EAAE,aAAa;IACnC,aAAa,EAAE,WAAW;IAC1B,mCAAmC;AACvC;;AAEA,qBAAqB;AACrB;IACI,UAAU;IACV,YAAY;IACZ,+CAA+C;IAC/C,sBAAsB;IACtB,oCAAoC;IACpC,2DAA2D;IAC3D,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,kBAAkB;IAClB,qFAAqF;IACrF,oCAAoC;IACpC,aAAa;AACjB;;AAEA,cAAc;AACd;IACI,qBAAqB;IACrB,uBAAuB;IACvB,+BAA+B;IAC/B,YAAY;AAChB;;AAEA;IACI,sBAAsB;IACtB,uBAAuB;IACvB,yBAAyB;IACzB,wBAAwB;IACxB,6CAA6C;IAC7C,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,cAAc;AAClB;AACA;IACI,gBAAgB;IAChB,+BAA+B;IAC/B,eAAe;IACf,gBAAgB;IAChB,gBAAgB;AACpB","sourcesContent":["/* カーソルを非表示にする */\r\n* {\r\n    cursor: none !important;\r\n}\r\n\r\n#cursor, #stalker {\r\n    position: fixed;\r\n    pointer-events: none;\r\n    transform: translate(-50%, -50%);\r\n    mix-blend-mode: difference;\r\n    pointer-events: none; /* クリックを無効化 */\r\n    z-index: 9999; /* 最前面に配置 */\r\n    transition: transform 0.1s ease-out;\r\n}\r\n\r\n/* カーソルのスタイル #894646*/\r\n#cursor {\r\n    width: 1px;\r\n    height: 26px;\r\n    transform: translate(300%, -15%) rotate(-40deg);\r\n    border: 1px solid #fff;\r\n    box-shadow: 0 0 3px rgba(25, 25, 25);\r\n    transition: width 0.3s ease-in-out, height 0.3s ease-in-out;\r\n    z-index: 10000;\r\n}\r\n\r\n#stalker {\r\n    width: 30px;\r\n    height: 30px;\r\n    border: 1px solid #fff;\r\n    border-radius: 50%;\r\n    transition: opacity 0.3s ease-in-out, width 0.3s ease-in-out, height 0.3s ease-in-out;\r\n    box-shadow: 0 0 4px rgba(25, 25, 25);\r\n    z-index: 9999;\r\n}\r\n\r\n/* ホバー時のスタイル */\r\n.cursor--hover {\r\n    width: 1px !important;\r\n    height: 40px !important;\r\n    border: 1px solid var(--border);\r\n    opacity: 0.5;\r\n}\r\n\r\n.stalker--hover {\r\n    width: 60px !important;\r\n    height: 60px !important;\r\n    border: 1px solid #894646;\r\n    background: var(--hover);\r\n 