        cursor: pointer;
        display: grid;
        justify-content: center;
        z-index: 100;
    }

    @keyframes fadeIn_bg {
        0% {
            opacity: 0;
            transform: translateY(15px);
            filter: blur(4px);
        }
        100% {
            opacity: 1;
            transform: translateY(0);
            filter: blur(0px);
        }
    }

    /* 閉じるボタンのスタイル */
    #close-button {
        font-size: 1.4rem;
        font-family: "Jost", sans-serif;
        position: absolute;
        top: 10px;
        right: 15px;
        cursor: pointer;
    }

    #close-button:hover,
    #close-button:focus {
        color: #636363;
        color: var(--accent);
    }

    /*==bot*/
    /*ラジオボタン装飾*/
    #chat-window input {
        /*-webkit-appearance: none;*/
        /* remove default */
        display: block;
        color: #fff;
        font-size: 1rem;
        margin-top: 0px;
        width: 15px;
        height: 15px;
        border-radius: 20px;
        cursor: pointer;
        /*vertical-align: middle;*/
        box-shadow: hsla(0, 0%, 10%, 0.2) 0px 0px 0px, inset hsla(0, 0%, 0%, 0.5) 0px 1px 3px 1px;
