,kBAAkB,EAAE,iBAAiB;QACzC;;QAEA;YACI,SAAS;YACT,iBAAiB;YACjB,gBAAgB;QACpB;;QAEA,aAAa;QACb;YACI,MAAM;YACN,cAAc;YACd,oBAAoB,EAAE,iBAAiB;YACvC,kBAAkB,EAAE,iBAAiB;QACzC;;QAEA;YACI,iBAAiB;YACjB,cAAc;QAClB;;QAEA;;YAEI,gBAAgB;QACpB;QACA;;YAEI,oBAAoB;YACpB,iCAAiC;YACjC,sCAAsC;QAC1C;;;QAGA;YACI,SAAS;YACT,aAAa;YACb,+BAA+B,EAAE,UAAU;YAC3C,0BAA0B,EAAE,UAAU;YACtC,SAAS,EAAE,eAAe;QAC9B;;QAEA;YACI,oDAAoD;YACpD,kBAAkB;YAClB,WAAW,EAAE,WAAW;YACxB,kBAAkB,EAAE,UAAU;YAC9B,aAAa,EAAE,sBAAsB;YACrC,qBAAqB,EAAE,aAAa;YACpC,mBAAmB,EAAE,aAAa;YAClC,SAAS;QACb;;;QAGA;YACI,cAAc;YACd,mBAAmB;YACnB,WAAW,EAAE,WAAW;YACxB,kBAAkB,EAAE,UAAU;YAC9B,aAAa,EAAE,oBAAoB;YACnC,qBAAqB,EAAE,SAAS;YAChC,mBAAmB,EAAE,cAAc;YACnC,SAAS;QACb;;QAEA;YACI,WAAW,EAAE,WAAW;YACxB,kBAAkB,EAAE,UAAU;YAC9B,WAAW;YACX,YAAY,EAAE,iBAAiB;YAC/B,aAAa,EAAE,uBAAuB;YACtC,uBAAuB;YACvB,mBAAmB;QACvB;;QAEA;;YAEI,WAAW;YACX,eAAe;YACf,eAAe;YACf,mBAAmB;QACvB;;QAEA;YACI,qBAAqB;YACrB,SAAS;QACb;;QAEA;;YAEI,eAAe;YACf,gBAAgB;YAChB,WAAW;YACX,YAAY;YACZ,iBAAiB;YACjB,gCAAgC;YAChC,eAAe,EAAE,gBAAgB;YACjC,gBAAgB;YAChB,sCAAsC;YACtC,qBAAqB;YACrB,8CAA8C;QAClD;QACA;YACI,oCAAoC;QACxC;;IAEJ","sourcesContent":["    /* ChatBot アイコンのスタイル */\r\n    #chat-icon {\r\n        font-family: \"Jost\", sans-serif;\r\n        font-weight: 400;\r\n        font-style: normal;\r\n        text-align: justify;\r\n        /*font-size: clamp(2rem, calc(2rem + 0.1vw), 2.4rem);\r\n        bottom: 15px;\r\n        width: clamp(20%, 25%, 30%);\r\n        height: clamp(10rem, 1vw, 12rem);\r\n        backdrop-filter: blur(4px);\r\n        border: 0;\r\n        border-radius: 0 30px 30px 0;\r\n        background-size: calc(200% - 10vw);\r\n        box-shadow: 1px 2px 3px var(--text-color);\r\n        background-position: center;\r\n        background-repeat: no-repeat;\r\n        position: absolute;*/\r\n        cursor: pointer;\r\n        display: grid;\r\n        justify-content: center;\r\n        z-index: 100;\r\n    }\r\n\r\n    @keyframes fadeIn_bg {\r\n        0% {\r\n            opacity: 0;\r\n            transform: translateY(15px);\r\n            filter: blur(4px);\r\n        }\r\n        100% {\r\n            opacity: 1;\r\n            transform: translateY(0);\r\n            filter: blur(0px);\r\n        }\r\n    }\r\n\r\n    /* 閉じるボタンのスタイル */\r\n    #close-button {\r\n        font-size: 1.4rem;\r\n        font-family: \"Jost\", sans-serif;\r\n        position: absolute;\r\n        top: 10px;\r\n        right: 15px;\r\n        cursor: pointer;\r\n    }\r\n\r\n    #close-button:hover,\r\n    #close-button:focus {\r\n        color: #636363;\r\n        color: var(--accent);\r\n    }\r\n\r\n    /*==bot*/\r\n    /*ラジオボタン装飾*/\r\n    #chat-window input {\r\n        /*-webkit-appearance: none;*/\r\n        /* remove default */\r\n        display: block;\r\n        color: #fff;\r\n        font-size: 1rem;\r\n        margin-top: 0px;\r\n        width: 15px;\r\n        height: 15px;\r\n        border-radius: 20px;\r\n        cursor: pointer;\r\n        /*vertical-align: middle;*/\r\n        box-shadow: hsla(0, 0%, 10%, 0.2) 0px 0px 0px, inset hsla(0, 0%, 0%, 0.5) 0px 1px 3px 1px;\r\n        background-image: -webkit-radial-gradient(rgb(255, 246, 204) 0%, rgb(255, 189, 102) 15%, hsla(29, 100%, 60%, 0.3) 28%, hsla(200, 100%, 30%, 0) 70%);\r\n        background-repeat: no-repeat;\r\n        transition: background-position .15s cubic-bezier(.8, 0, 1, 1), transform .20s cubic-bezier(.8, 0, 1, 1);\r\n        outline: none;\r\n        animation: fadeIn_bg 0.5s ease forwards;\r\n    }\r\n\r\n    input:checked {\r\n        transition: background-position .2s .15s cubic-bezier(0, 0, .2, 1), transform .20s cubic-bezier(0, 0, .2, 1);\r\n    }\r\n\r\n    input:active {\r\n        -webkit-transform: scale(1.5);\r\n        transform: scale(1);\r\n        transition: transform .1s cubic-bezier(0, 0, .2, 1);\r\n    }\r\n\r\n    /* The up/down direction logic */\r\n    input,\r\n    input:active {\r\n        background-position: 0 20px;\r\n    }\r\n\r\n    input:checked {\r\n        background-position: 0 0;\r\n    }\r\n\r\n    input:checked~input,\r\n    input:checked~input:active {\r\n        background-position: 0 -20px;\r\n    }\r\n\r\n    /* ベーススタイル */\r\n    .chatbot-container {\r\n        width: 100%;\r\n        margin: 0 auto;\r\n    }\r\n\r\n    /*    .chatbot-container p {\r\n        font-size: clamp(0.85rem, calc(0.85rem + 0.5vw), 1rem);\r\n    }*/\r\n\r\n    /* ChatBot ウィンドウのスタイル */\r\n    #chat-window {\r\n        width: calc(100% - 100px);\r\n        height: calc(75% - 130px);\r\n        position: absolute;\r\n        bottom: 0px;\r\n        left: 20px;\r\n        padding: 4vw 5vw;\r\n        backdrop-filter: blur(3px);\r\n        box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.4);\r\n        border-radius: 20px;\r\n        display: none;\r\n        overflow-y: scroll;\r\n        animation: fadeIn_left 1.5s ease forwards;\r\n        z-index: 200;\r\n    }\r\n\r\n    .chat-window {\r\n        height: 130px;\r\n        overflow-y: scroll;\r\n        box-shadow: hsla(0, 0%, 10%, 0.2) 0px 1px 2px, inset hsla(0, 0%, 0%, 0.5) 0 1px 3px 1px;\r\n        background-color: rgba(252, 250, 245, 0.85);\r\n    }\r\n\r\n    /* チャットメッセージのスタイル */\r\n    #chat {\r\n        margin: 0.85em;\r\n        line-height: 1.5em;\r\n        letter-spacing: 0.1em;\r\n        padding: 0.75em;\r\n    }\r\n\r\n    .bot {\r\n        margin-bottom: 0.8em;\r\n        border-bottom: 1px solid;\r\n    }\r\n\r\n    .input-container {\r\n        margin-top: 20px;\r\n        margin-left: 20px;\r\n        display: grid;\r\n        justify-content: center;\r\n    }\r\n\r\n    .radio-options {\r\n        width: 50%;\r\n        height: 100%;\r\n    }\r\n\r\n    .radio-options label {\r\n        display: block;\r\n        width: fit-content;\r\n        margin: 5px;\r\n    }\r\n\r\n    /* キーワード選択肢 */\r\n    .keyword-options {\r\n        position: relative;\r\n        top: 3%;\r\n        margin-left: 0;\r\n    }\r\n\r\n    .keyword-options label {\r\n        width: fit-content;\r\n        margin: 0 0 5px;\r\n    }\r\n\r\n    /*ラジオボタン*/\r\n    .chat-options {\r\n        width: 100%;\r\n        height: 240px;\r\n        margin-top: 10px;\r\n        display: grid;\r\n        grid-template-columns: 1fr 1fr;\r\n    }\r\n\r\n    .chat-options select,\r\n    .chat-options input {\r\n        width: 10px;\r\n        padding: 10px;\r\n        margin-right: 0;\r\n        margin-bottom: 3px;\r\n        position: relative;\r\n        top: 0px;\r\n    }\r\n\r\n    .button-container {\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n    }\r\n\r\n    .send-button,\r\n    .clear-button {\r\n        padding: 2px 15px;\r\n        font-size: 1.2rem;\r\n        font-weight: bold;\r\n        width: 85pt;\r\n        height: 30pt;\r\n        text-align: center;\r\n        margin: 0 auto;\r\n        border: 2px solid;\r\n        border-radius: 5px;\r\n        cursor: pointer;\r\n        outline: none;\r\n        border: double 4px var(--accent-color);\r\n        transition: all .4s ease-in-out;\r\n    }\r\n\r\n    span.wpcf7-not-valid-tip:last-child {\r\n        color: red;\r\n        top: 90%;\r\n    }\r\n\r\n    .clear-button {\r\n        background: rgb(0, 209, 171);\r\n    }\r\n\r\n    .send-button {\r\n        background: var(--bg-color);\r\n    }\r\n\r\n    .send-button:hover,\r\n    .clear-button:hover {\r\n        color: #777777;\r\n        background-color: rgba(200, 200, 200, 0.3);\r\n    }\r\n\r\n    .panel {\r\n        position: relative;\r\n    }\r\n\r\n    @media screen and (min-width: 600px) and (max-width: 1299px) {\r\n\r\n        /* ChatBot アイコンのスタイル */\r\n        #chat-icon {\r\n            font-size: 1.5rem;\r\n            bottom: 20px;\r\n            width: 7em;\r\n            height: 5em;\r\n        }\r\n\r\n        #chat-icon::before {\r\n            content: \"こちらで質問ができます\";\r\n            position: absolute;\r\n            font-size: 0.9rem;\r\n            bottom: -4.5%;\r\n            left: 50%;\r\n            transform: translate(-50%, -50%);\r\n            white-space: nowrap;\r\n        }\r\n\r\n        /* 閉じるボタンのスタイル */\r\n        #close-button {\r\n            top: 10px;\r\n            right: 15px;\r\n            font-size: 1.5rem;\r\n        }\r\n\r\n        /*==bot*/\r\n        /*ラジオボタン装飾*/\r\n        #chat-window input {\r\n            font-size: 1rem;\r\n            margin-top: 0px;\r\n            width: 15px;\r\n            height: 15px;\r\n            border-radius: 20px;\r\n            animation: fadeIn_bg 0.5s ease forwards;\r\n        }\r\n\r\n        /* ChatBot ウィンドウのスタイル */\r\n        #chat-window {\r\n            width: calc(85vmin - 100px);\r\n            height: calc(85% - 100px);\r\n            bottom: 20px;\r\n            left: 20px;\r\n            padding: 4vmin 5vmin 0;\r\n            border-radius: 20px;\r\n            animation: fadeIn_left 1.5s ease forwards;\r\n        }\r\n\r\n        .chat-window {\r\n            height: 200px;\r\n        }\r\n\r\n        /* チャットメッセージのスタイル */\r\n        #chat {\r\n            margin: 0.85em;\r\n            line-height: 1.5em;\r\n            letter-spacing: 0.1em;\r\n            padding: 0.75em;\r\n        }\r\n\r\n        .bot {\r\n            font-size: clamp(0.8rem, calc(0.8rem + 0.5vw), 1rem);\r\n            line-height: 1.4;\r\n        }\r\n\r\n        .chat-options {\r\n            height: 270px;\r\n        }\r\n\r\n        .input-container {\r\n            margin-top: 20px;\r\n            margin-left: 20px;\r\n        }\r\n\r\n        .radio-options {\r\n            padding: 0 calc(2vw - 20px) 0;\r\n        }\r\n\r\n        .radio-options label {\r\n            margin-top: 5px;\r\n        }\r\n\r\n        /* キーワード選択肢 */\r\n        .keyword-options label {\r\n            margin-top: 0px;\r\n        }\r\n\r\n        /*ラジオボタン*/\r\n        .chat-options select,\r\n        .chat-options input {\r\n            width: 10px;\r\n            padding: 10px;\r\n            margin-right: 0;\r\n            margin-bottom: 10px;\r\n            top: 0px;\r\n        }\r\n\r\n        .send-button,\r\n        .clear-button {\r\n            width: 95pt;\r\n            height: 35pt;\r\n            margin: 5% auto 0;\r\n            padding: calc(2vw - 20px) 2rem 0;\r\n            transition: all .4s ease-in-out;\r\n            border: 2px solid;\r\n        }\r\n\r\n        span.wpcf7-not-valid-tip:last-child {\r\n            top: 90%;\r\n        }\r\n    }\r\n\r\n    @media screen and (min-width: 1300px) {\r\n\r\n        /* ChatBot アイコンのスタイル */\r\n        #chat-icon {\r\n            font-size: 1.8rem;\r\n            position: absolute;\r\n            top: 50%;\r\n            left: -50%;\r\n            width: 100%;\r\n            padding: 0.5vmin;\r\n            transform: rotate(90deg) translate(0, -4.5vmin);\r\n            border-top: 1px solid;\r\n            border-image: url(\"../assets/images/text-gold.png\") 100;\r\n            background: url(\"../assets/images/mobile-main-second.png\");\r\n            box-shadow: 0px -3px 10px rgba(46, 46, 46, 0.85);\r\n            z-index: 1;\r\n            /*bottom: 0px;\r\n            width: 15vmin;\r\n            height: 8vmin;\r\n            padding: 0 1rem;\r\n            transition: all 0.5s ease;\r\n            animation: fadeIn_bg 4s ease forwards;*/\r\n        }\r\n        #chat-icon:hover {\r\n            transform: rotate(90deg) translate(0, -5vmin);\r\n        }\r\n        #chat-icon span {\r\n            background-clip: text;\r\n            -webkit-background-clip:text;\r\n            color: transparent;\r\n            background: url(\"../assets/images/text-gold.png\") 100;\r\n        }\r\n\r\n        /*#chat-icon::before {\r\n            content: \"こちらで質問ができます\";\r\n            font-size: clamp(0.8rem, calc(0.8rem + 0.5vw), 0.9rem);\r\n            position: absolute;\r\n            bottom: -5.5%;\r\n            left: 50%;\r\n            transform: translate(-50%, -50%);\r\n            white-space: nowrap;\