___CSS_LOADER_EXPORT___.push([module.id, `/*==アコーディオン==*/
.accordion-wrap {
    width: calc(60% - 50px);
    position: relative;
    padding: 1rem;
    margin: 0 auto 1em;
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
}

.accordion-item {
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    align-items: center;
    justify-items: center;
    border: 1px solid var(--text);
    box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.85);
    background: rgba(5, 2, 8, 0.1);
}

.accordion-header {
    cursor: pointer;
    display: block;
    align-content: center;
    position: relative;
    width: 100%;
    padding-left: 10%;
    border-bottom: 1px solid var(--text);
    transition: color 100ms;
}

.accordion-header:hover,
.accordion-header:active {
    background-color: rgba(205, 205, 205, 0.45);
}


.accordion-header h3 {
    font-size: clamp(1.8rem, 1.381rem + 0.52vw, 2rem);
    font-weight: 600;
    border-left: 1px solid var(--text);
    padding: 0 1rem;
}

/*in-text*/
.accordion-text {
    max-height: 0;
    overflow: hidden;
    padding: 0;
    transition: max-height 0.5s ease, padding 0.4s ease;
}

.accordion-item.active .accordion-text {
    overflow: auto;
    min-height: fit-content;
}

.accordion-text h4 {
    font-size: clamp(1.6rem, 1.181rem + 0.52vw, 1.8rem);
    font-weight: 600;
    color: var(--text);
    margin: 1vmin 10vmin 0;
    mix-blend-mode: normal;
}

.accordion-text p {
    font-size: clamp(1.4rem, 0.981rem + 0.52vw, 1.6rem);
    width: 80%;
    font-weight: 600;
    margin: 0 auto;
    letter-spacing: 0.1rem;
    text-shadow: none;
}

.contact #section--third .accordion-text p {
    width: 80%;
}

/*== プラスアイコン（＋）の構造 ==*/
.fa {
    position: absolute;
    width: 2em;
    height: 2em;
    top: 30%;
    left: 1.5vmin;
}

.fa span {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--text);
    transform: translateY(-50%);
}

.fa span:nth-child(2) {
    left: 50%;
    top: 0;
    width: 2px;
    height: 100%;
    transform: translateX(-50%);
    transition: transform 0.4s ease;
}

/* アクティブ時：縦線を非表示にして ＋→− に */
.fa.rotate-fa span:nth-child(2) {
    transform: translateX(-50%) scaleY(0);
}


@media screen and (max-width: 600px) {
    .accordion-wrap {
        width: 80%;
        padding: 0.15rem;
        margin: 1em auto;
    }

    .accordion-header {
        height: 45pt;
        border-radius: 10px;
    }

    .accordion-header:active {
        box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.5);
        background-color: rgba(205, 205, 205, 0.45);
    }

    .accordion-item {
        border-radius: 10px;
        margin-bottom: 2vmin;
        background-color: rgba(225, 222, 248, 0.2);
        box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
    }

    .accordion-text p {
        font-size: clamp(1.4rem, 1.335rem + 0.33vw, 1.6rem);
        width: 90%;
    }
}`, "",{"version":3,"sources":["webpack://./src/style/accordion.css"],"names":[],"mappings":"AAAA,cAAc;AACd;IACI,uBAAuB;IACvB,kBAAkB;IAClB,aAAa;IACb,kBAAkB;IAClB,kCAAkC;IAClC,0BAA0B;AAC9B;;AAEA;IACI,WAAW;IACX,cAAc;IACd,aAAa;IACb,sBAAsB;IACtB,eAAe;IACf,mBAAmB;IACnB,qBAAqB;IACrB,6BAA6B;IAC7B,iDAAiD;IACjD,8BAA8B;AAClC;;AAEA;IACI,eAAe;IACf,cAAc;IACd,qBAAqB;IACrB,kBAAkB;IAClB,WAAW;IACX,iBAAiB;IACjB,oCAAoC;IACpC,uBAAuB;AAC3B;;AAEA;;IAEI,2CAA2C;AAC/C;;;AAGA;IACI,iDAAiD;IACjD,gBAAgB;IAChB,kCAAkC;IAClC,eAAe;AACnB;;AAEA,UAAU;AACV;IACI,aAAa;IACb,gBAAgB;IAChB,UAAU;IACV,mDAAmD;AACvD;;AAEA;IACI,cAAc;IACd,uBAAuB;AAC3B;;AAEA;IACI,mDAAmD;IACnD,gBAAgB;IAChB,kBAAkB;IAClB,sBAAsB;IACtB,sBAAsB;AAC1B;;AAEA;IACI,mDAAmD;IACnD,UAAU;IACV,gBAAgB;IAChB,cAAc;IACd,sBAAsB;IACtB,iBAAiB;AACrB;;AAEA;IACI,UAAU;AACd;;AAEA,sBAAsB;AACtB;IACI,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,QAAQ;IACR,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,OAAO;IACP,WAAW;IACX,WAAW;IACX,6BAA6B;IAC7B,2BAA2B;AAC/B;;AAEA;IACI,SAAS;IACT,MAAM;IACN,UAAU;IACV,YAAY;IACZ,2BAA2B;IAC3B,+BAA+B;AACnC;;AAEA,2BAA2B;AAC3B;IACI,qCAAqC;AACzC;;;AAGA;IACI;QACI,UAAU;QACV,gBAAgB;QAChB,gBAAgB;IACpB;;IAEA;QACI,YAAY;QACZ,mBAAmB;IACvB;;IAEA;QACI,gDAAgD;QAChD,2CAA2C;IAC/C;;IAEA;QACI,mBAAmB;QACnB,oBAAoB;QACpB,0CAA0C;QAC1C,0CAA0C;IAC9C;;IAEA;QACI,mDAAmD;QACnD,UAAU;IACd;AACJ","sourcesContent":["/*==アコーディオン==*/\r\n.accordion-wrap {\r\n    width: calc(60% - 50px);\r\n    position: relative;\r\n    padding: 1rem;\r\n    margin: 0 auto 1em;\r\n    -webkit-backdrop-filter: blur(3px);\r\n    backdrop-filter: blur(3px);\r\n}\r\n\r\n.accordion-item {\r\n    width: 100%;\r\n    margin: 0 auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n    cursor: pointer;\r\n    align-items: center;\r\n    justify-items: center;\r\n    border: 1px solid var(--text);\r\n    box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.85);\r\n    background: rgba(5, 2, 8, 0.1);\r\n}\r\n\r\n.accordion-header {\r\n    cursor: pointer;\r\n    display: block;\r\n    align-content: center;\r\n   