.arrow {
   position: relative;
   display: inline-block;
   width: 33px;
   height: 5vmin;
}

.arrow::before,
.arrow::after {
   content: "";
   position: absolute;
   top: calc(50% - 2px);
   right: 0;
   width: 20px;
   height: 2px;
   transform-origin: calc(100% - 1px) 50%;
   background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat center center/cover;
}

