.icon-grid {
   display: grid;
   width: 35%;
   position: absolute;
   left: 10vmin;
   bottom: 20vmin;
   grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
   gap: 1rem;
   padding: 1.5rem;
   background: var(--border);
   border-radius: 8px;
}

/* Individual Icon Item */
.icon-item {
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: var(--border);
   border-radius: 50%;
   width: 65px;
   height: 65px;
   margin: 0 auto;
   transition: transform 0.3s ease, background-color 0.3s ease;
}

/* Icon Styling */
.icon-item i {
   font-size: 5rem;
   color: var(--text);
}

.icon-item span {
   font-size: clamp(1rem, 0.667rem + 0.44vw, 1.2rem);
   color: var(--text);
   height: 20px;
   position: absolute;
   margin-top: 5.5vmin;
}

/* Hover Effect */
.icon-item:hover {
   transform: scale(1.1);
}


/*==About & Service */
