.modal .accordion-wrap {
   top: -5vmin;
   left: 9.5vmin;
   padding: 1rem;
   margin-bottom: 1rem;
   mix-blend-mode: normal;
}

.policy .modal ol .accordion-wrap h3 {
   font-size: 2rem;
   margin-top: 0.5vmin;
}

.policy .modal ol .accordion-wrap .fa span {
   top: 2.75vmin;
