___CSS_LOADER_EXPORT___.push([module.id, `@media only screen and (max-width:599px) {
    footer {
        position: relative;
        height: 40svh;
        padding-left: 6vmin;
        border-top: 1px solid var(--border);
        background:
            linear-gradient(to top, rgba(0, 0, 0, 0.35) 0%,
                rgba(0, 0, 0, 0.35) 100%),
            url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat top center/cover;
        z-index: 100;
    }

    footer::before {
        content: "";
        height: 100%;
        border-right: 1px solid var(--border);
        position: absolute;
        left: 8vmin;
    }

    header a::after,
    footer a::after {
        content: "";
        width: 100%;
        height: 1px;
        bottom: 5px;
        position: absolute;
        left: 0;
        background-color: var(--bg);
        transform: scale(0, 1);
        transform-origin: right top;
        transition: transform 0.3s ease;
    }

    header a:hover::after,
    footer a:hover::after {
        transform-origin: left top;
        transform: scale(1, 1);
    }

    footer a {
        font-size: 1.8rem;
        margin: 1rem 0 0 2rem;
        padding: 0.5rem;
        text-align: left;
        display: inline-block;
    }

    .fo-a a {
        font-size: 1.4rem;
    }

    footer a:hover,
    footer a:focus {
        opacity: 0.95;
    }

    .foot {
        font-family: sans-serif;
        font-size: clamp(1.2rem, 1.127rem + 0.36vw, 1.4rem);
        color: var(--bg);
        position: absolute;
        /* 位置を固定 */
        left: 3%;
        bottom: 5vmin;
        transform-origin: left bottom;
        /* 左下を基準に回転 */
        white-space: nowrap;
        writing-mode: vertical-rl;
        /* 文字の向きを縦書きに */
    }
