, 113)*/\r\n   --accent: #A07171;\r\n   /*cadet blue rgba(95, 158, 160)*/\r\n   --ac-blue: #5F9EA0;\r\n   /*bt (117, 117, 117)*/\r\n   --bt: #757575;\r\n   /*border */\r\n   --border: rgba(199, 199, 199, 0.5);\r\n   color-scheme: light dark;\r\n}\r\n\r\n::selection {\r\n   background-color: rgba(160, 113, 113, 0.5);\r\n}\r\n\r\n/*===================== Scrollbar =====================*/\r\n::-webkit-scrollbar {\r\n   width: 0.5rem;\r\n   background: rgba(40, 0, 0, 0.4);\r\n}\r\n\r\n::-webkit-scrollbar-track {\r\n   box-shadow: 0 0 0px rgba(255, 255, 255, 0) inset;\r\n   background: rgba(245, 252, 246, 0);\r\n   mix-blend-mode: color-burn;\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n   box-shadow: 0 0 2px 1px rgba(31, 30, 30, 0.76) inset;\r\n   background: rgba(20, 20, 20, 0.3);\r\n   mix-blend-mode: color-burn;\r\n}\r\n\r\n/*===================== Scrollbar =====================*/\r\n\r\n\r\n/*===================== body =====================*/\r\n.pc {\r\n   display: block;\r\n}\r\n.mobile {\r\n   display: none;\r\n}\r\n\r\n@media screen and (min-width: 1300px) {\r\n   body {\r\n   overflow: hidden;\r\n   }\r\n}\r\n\r\nbody {\r\n   margin: 0;\r\n   padding: 0;\r\n   box-sizing: border-box;\r\n   font-family: \"Jost\", serif;\r\n   font-weight: 500;\r\n   font-style: normal;\r\n   text-align: justify;\r\n   width: 100vw;\r\n   height: 100vh;\r\n   color: var(--bg);\r\n   background: var(--bg);\r\n   background: url(\"../assets/images/pd-body-bg.jpg\") no-repeat center center/cover;\r\n/*   background: url(/img/pd-bg-img05.png);\r\n   background-position: center;\r\n   background-repeat: no-repeat;\r\n   background-size: contain;*/\r\n   z-index: 1;\r\n}\r\nbody::before {\r\n   content: \"\";\r\n   width: 100%;\r\n   height: 100%;\r\n   position: fixed;\r\n   border: 2vh solid;\r\n   border-left: 2vw solid;\r\n   border-right: 2vw solid;\r\n   border-image: url(\"../assets/images/mobile-main-second.png\") 100;\r\n   z-index: 10;\r\n   pointer-events: none;\r\n   /* クリックを無効化 */\r\n   box-shadow: inset -3px -3px 10px rgba(46, 46, 46, 0.85);\r\n}\r\n\r\nbody::after {\r\n   content: \"\";\r\n   position: fixed;\r\n   top: 2vh;\r\n   left: 2vw;\r\n   width: 96vw;\r\n   height: 96vh;\r\n   border: 1px solid;\r\n   pointer-events: none;\r\n   z-index: 11;\r\n}\r\n\r\n/*===================== h1~p =====================*/\r\n\r\nh1 {\r\n   /*font-size: 62.5%;=16px 12rem=120px*/\r\n   font-size: clamp(2.8rem, 1.323rem + 1.23vw, 3.2rem);\r\n   font-family: \"Jost\", serif;\r\n   font-style: normal;\r\n   font-optical-sizing: auto;\r\n   font-weight: 500;\r\n   width: fit-content;\r\n   line-height: 4rem;\r\n   letter-spacing: 0.6rem;\r\n}\r\n\r\nh1 span {\r\n   font-size: 2.4rem;\r\n   letter-spacing: 0.4rem;\r\n}\r\n\r\nh2,\r\nh3,\r\nh4 {\r\n   font-family: \"Jost\", serif;\r\n   font-optical-sizing: auto;\r\n   font-weight: 400;\r\n   font-style: normal;\r\n   color:  light-dark(var(--bg));\r\n}\r\n\r\nh2 {\r\n   /*font-size: 62.5%;=16px 6rem=62px*/\r\n   font-weight: 500;\r\n   font-size: clamp(2rem, 0.923rem + 1.23vw, 2.8rem);\r\n   width: fit-content;\r\n   line-height: 3rem;\r\n   letter-spacing: 0.5rem;\r\n}\r\n\r\n/* gold text bg */\r\n.pc_menu a,\r\n#chat-icon span,\r\nh1,\r\nh2,\r\n.fa-power-off:before,\r\n.audio span,\r\n.left-text,\r\n.right-text,\r\n.bt-text,\r\n.bottom-btn,\r\n.card-content h3,\r\n.card-content h3::before {\r\n   background: url(\"../assets/images/text-gold.png\") no-repeat center center/cover;\r\n   background-clip: text;\r\n   -webkit-background-clip: text;\r\n   color: transparent;\r\n   /*mix-blend-mode: luminosity;*/\r\n}\r\n\r\n/* gold border */\r\nbody::after,\r\n.right-text::before,\r\n.card-content h3,\r\nmain .button-a::before,\r\nmain .button-a::after {\r\n   border-image: url(\"../assets/images/text-gold.png\") 100;\r\n}\r\n\r\n\r\n.section h1,\r\n.section h2 {\r\n   position: relative;\r\n   top: 10vmin;\r\n   left: 10rem;\r\n}\r\n\r\n.section h2 {\r\n   top: 11vmin;\r\n}\r\n\r\nh3 {\r\n   /*font-size